<template>
  <el-card class="login-card">
    <el-tabs v-model="activePanel" stretch @tab-click="clearForm">
      <el-tab-pane label="登陆" name="login">
        <el-form
          class="login-form"
          :model="form"
          :rules="form.loginRules"
          ref="loginForm"
          @submit.native.prevent="login"
        >
          <el-form-item prop="account">
            <template slot="label">
              <strong>账号</strong>
            </template>
            <el-input
              v-model="form.account"
              placeholder="请输入账号"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>
          <el-form-item prop="password">
            <template slot="label">
              <strong>密码</strong>
            </template>
            <el-input
              v-model="form.password"
              placeholder="请输入密码"
              type="password"
              prefix-icon="el-icon-lock"
              show-password
              clearable
            />
          </el-form-item>
          <el-form-item>
            <el-button
              :loading="form.loading"
              class="login-button"
              type="primary"
              native-type="submit"
              block
              >登陆</el-button
            >
          </el-form-item>
          <el-link type="info" class="forgot-password">忘记密码？</el-link>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="注册" name="register">
        <el-form
          class="login-form"
          :model="form"
          :rules="form.registerRules"
          ref="registerForm"
          @submit.native.prevent="register"
        >
          <el-form-item prop="account">
            <template slot="label">
              <strong>账号</strong>
            </template>
            <el-input
              v-model="form.account"
              placeholder="请输入账号"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>
          <el-form-item prop="email">
            <template slot="label">
              <strong>邮箱地址</strong>
            </template>
            <el-input
              v-model="form.email"
              placeholder="请输入邮箱"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>
          <el-form-item prop="nickname">
            <template slot="label">
              <strong>昵称（可选）</strong>
            </template>
            <el-input
              v-model="form.nickname"
              placeholder="请输入昵称"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>
          <el-form-item prop="password">
            <template slot="label">
              <strong>密码</strong>
            </template>
            <el-input
              v-model="form.password"
              placeholder="请输入密码"
              type="password"
              prefix-icon="el-icon-lock"
              show-password
              clearable
            />
          </el-form-item>
          <el-form-item prop="confirmPassword">
            <template slot="label">
              <strong>确认密码</strong>
            </template>
            <el-input
              v-model="form.confirmPassword"
              placeholder="请确认密码"
              type="password"
              prefix-icon="el-icon-lock"
              show-password
              clearable
            />
          </el-form-item>
          <el-form-item>
            <el-button
              :loading="form.loading"
              class="login-button"
              type="primary"
              native-type="submit"
              block
            >
              注册
            </el-button>
          </el-form-item>
          <el-link type="info" class="forgot-password">忘记密码？</el-link>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </el-card>
</template>

<script>
export default {
  data() {
    const basicRules = {
      account: [{ required: true, message: "请输入账号", trigger: "blur" }],
      password: [{ required: true, message: "请输入密码", trigger: "blur" }],
    };
    return {
      /** @type {'login'|'register'} */
      activePanel: "login",
      form: {
        account: "",
        email: "",
        nickname: "",
        password: "",
        confirmPassword: "",
        loading: false,
        loginRules: basicRules,
        registerRules: {
          ...basicRules,
          email: [
            {
              required: true,
              message: "请输入邮箱",
              trigger: "blur",
            },
            {
              validator: (_rule, value, callback) => {
                if (this.validateEmail(value)) callback();
                else callback(new Error("错误的邮箱格式"));
              },
              trigger: "blur",
            },
          ],
          confirmPassword: [
            {
              required: true,
              message: "请确认密码",
              trigger: "blur",
            },
            {
              validator: (_rule, val, callback) => {
                if (this.form.password === val) callback();
                else callback(new Error("两次密码不一致"));
              },
            },
          ],
        },
      },
    };
  },
  methods: {
    clearForm() {
      this.form.account = "";
      this.form.email = "";
      this.form.nickname = "";
      this.form.password = "";
      this.form.confirmPassword = "";
      this.form.loading = false;
    },
    /**
     * validate the E-mail in RFC 5322
     *
     * @param {string} email - E-mail address to be validated
     * @returns {boolean} if email meet RFC 5322 standard
     */
    validateEmail(email) {
      return String(email)
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        );
    },
    async login() {
      // 执行表单验证
      let passed;
      this.$refs.loginForm.validate((res) => (passed = res));
      console.log("LoginForm login() passed validation?", passed);
      if (!passed) return;

      this.form.loading = true;
      const params = new URLSearchParams();
      params.append("account", this.form.account);
      params.append("password", this.form.password);

      // 给我同步
      await this.$http
        .get("/UserLogin?" + params)
        .then((res) => {
          console.log("LoginForm login() /UserLogin?" + params, res);
          this.$message({
            showClose: true,
            message: res.data,
            type: res.status == 200 ? "success" : "error",
          });

          if (res.status == 200) {
            this.$http
              .get("/GetUserMessage")
              .then((res) => {
                console.log("LoginForm.vue /getUserMessage", res);
                if (res.status == 200) {
                  this.$store.commit("login", res.data);
                  this.$router.push({ name: "HomeView" });
                }
              })
              .catch((err) => {
                console.error("LoginForm.vue /getUserMessage", err);
              });
          }
        })
        .catch((err) => {
          console.error("LoginForm login() /UserLogin?" + params, err);
          this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
        });

      this.form.loading = false;
    },

    async register() {
      // 执行表单验证
      let passed;
      this.$refs.registerForm.validate((res) => (passed = res));
      console.log("LoginForm register() passed validation?", passed);
      if (!passed) return;

      this.form.loading = true;
      const params = new URLSearchParams();
      params.append("account", this.form.account);
      params.append("password", this.form.password);
      params.append("email", this.form.email);
      // 如果没有提供 nickname 就使用 account
      params.append(
        "nick_name",
        this.form.nickname == "" ? this.form.account : this.form.nickname
      );

      await this.$http
        .get("/UserRegister?" + params)
        .then((res) => {
          console.log("LoginForm register() /UserRegister?" + params, res);
          this.$message({
            showClose: true,
            message: res.data,
            type: res.status == 200 ? "success" : "error",
          });
          if (res.status == 200) {
            this.activePanel = "login";
            this.form.password = "";
          }
        })
        .catch((err) => {
          console.error("LoginForm register() /UserRegister?" + params, err);
          this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
        });

      this.form.loading = false;
    },
  },
  mounted() {},
};
</script>

<style lang="scss" scoped>
.login-card {
  display: flex;
  justify-content: center;
  align-items: center;
  .login-form {
    width: 350px;
    .login-button {
      width: 100%;
      margin-top: 20px;
    }
  }
}
</style>