import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    auth: false,
    account: '',
    avatarPath: '',
    email: '',
    nickname: ''
  },
  getters: {},
  mutations: {
    login(state, {
      account,
      avatar_path,
      email,
      nick_name
    }) {
      state.auth = true
      state.account = account
      state.avatarPath = avatar_path
      state.email = email
      state.nickname = nick_name
    },
    logout(state) {
      state.auth = false
      state.account = ''
      state.avatarPath = ''
      state.email = ''
      state.nickname = ''
    }
  },
  actions: {},
  modules: {}
})