<template>
  <div id="app">
    <nav v-if="$route.name != 'LoginView'">
      <router-link to="/">Home</router-link> |
      <router-link to="/login">Login</router-link>
    </nav>
    <router-view />
  </div>
</template>

<script>
export default {
  mounted() {
    this.$http
      .get("/GetUserMessage")
      .then((res) => {
        console.log("App.vue /getUserMessage", res);
        if (res.status == 200) this.$store.commit("login", res.data);
      })
      .catch((err) => {
        console.error("App.vue /getUserMessage", err);
        this.$store.commit("logout");
        this.$router.push({ name: "LoginView" });
      })
      .finally(() => {
        this.$router.beforeEach((to, _from, next) => {
          if (to.name != "LoginView" && !this.$store.state.auth) {
            console.log("beforeEach() redirect to LoginView");
            next({
              name: "LoginView",
            });
          }
          next();
        });
      });
  },
};
</script>

