<template>
  <div class="home">
    <el-card class="card">
      <el-row :gutter="40">
        <el-col :span="6">
          <img
            v-if="!uploadAvatar.visible"
            class="avatar"
            alt="Avatar"
            :src="'http://' + $store.state.avatarPath"
          />
          <el-upload
            v-else
            class="avatar-uploader"
            :action="$http.defaults.baseURL + '/UploadAvatar'"
            name="avatar"
            with-credentials
            :show-file-list="false"
            :before-upload="beforeAvatarUpload"
            :http-request="onAvatarUpload"
          >
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-col>
        <el-col :span="18">
          <el-descriptions
            title="用户信息"
            :colon="false"
            direction="vertical"
            size="medium"
            border
          >
            <template slot="extra">
              <el-button
                type="primary"
                size="small"
                @click="updatePasswordForm.dialogVisible = true"
              >
                修改密码
              </el-button>
              <el-button
                :type="uploadAvatar.visible ? 'info' : 'primary'"
                size="small"
                @click="uploadAvatar.visible = !uploadAvatar.visible"
              >
                {{ uploadAvatar.visible ? "取消修改" : "修改头像" }}
              </el-button>
            </template>
            <el-descriptions-item label="账号">
              {{ $store.state.account }}
            </el-descriptions-item>
            <el-descriptions-item label="邮箱">
              {{ $store.state.email }}
            </el-descriptions-item>
            <el-descriptions-item label="昵称">
              {{ $store.state.nickname }}
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
      </el-row>
    </el-card>

    <el-dialog
      center
      title="修改密码"
      :visible.sync="updatePasswordForm.dialogVisible"
      @close="clearUpdatePasswordForm"
      width="400px"
    >
      <el-form
        :model="updatePasswordForm"
        :rules="updatePasswordForm.rules"
        ref="updatePasswordForm"
      >
        <el-form-item prop="oldPassword">
          <template slot="label">
            <strong>旧密码</strong>
          </template>
          <el-input
            v-model="updatePasswordForm.oldPassword"
            placeholder="请输入旧密码"
            type="password"
            prefix-icon="el-icon-lock"
            show-password
            clearable
          />
        </el-form-item>
        <el-form-item prop="newPassword">
          <template slot="label">
            <strong>新密码</strong>
          </template>
          <el-input
            v-model="updatePasswordForm.newPassword"
            placeholder="请输入新密码"
            type="password"
            prefix-icon="el-icon-lock"
            show-password
            clearable
          />
        </el-form-item>
        <el-form-item prop="confirmNewPassword">
          <template slot="label">
            <strong>确认新密码</strong>
          </template>
          <el-input
            v-model="updatePasswordForm.confirmNewPassword"
            placeholder="请确认新密码"
            type="password"
            prefix-icon="el-icon-lock"
            show-password
            clearable
          />
        </el-form-item>
      </el-form>
      <template slot="footer">
        <el-button
          :loading="updatePasswordForm.loading"
          type="primary"
          style="width: 100%"
          @click="updatePassword"
        >
          修改密码
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
// @ is an alias to /src

export default {
  data() {
    const requiredRule = (msg) => {
      return {
        required: true,
        message: msg,
        trigger: "blur",
      };
    };
    return {
      uploadAvatar: {
        visible: false,
      },
      updatePasswordForm: {
        loading: false,
        dialogVisible: false,
        oldPassword: "",
        newPassword: "",
        confirmNewPassword: "",
        rules: {
          oldPassword: [requiredRule("请输入旧密码")],
          newPassword: [
            requiredRule("请输入新密码"),
            {
              validator: (_rule, val, callback) => {
                if (this.updatePasswordForm.oldPassword !== val) callback();
                else callback(new Error("新旧密码不能一样"));
              },
            },
          ],
          confirmNewPassword: [
            requiredRule("去重复新密码"),
            {
              validator: (_rule, val, callback) => {
                if (this.updatePasswordForm.newPassword === val) callback();
                else callback(new Error("两次密码不一致"));
              },
            },
          ],
        },
      },
    };
  },
  methods: {
    /**
     * 手动上传头像
     *
     * @param {object} upload
     * @param {string} upload.action
     * @param {File} upload.file
     * @param {string} upload.filename
     */
    onAvatarUpload(upload) {
      console.log(upload.filename, upload.file.type, upload.file.size);
      const formData = new FormData();
      formData.append("avatar", upload.file);
      axios
        .post("/UploadAvatar", formData)
        .then((res) => {
          console.log("HomeView.vue onAvatarUpload", res);
          this.$message({
            showClose: true,
            message: res.data,
            type: res.status == 200 ? "success" : "error",
          });
          if (res.status == 200) {
            this.$http
              .get("/GetUserMessage")
              .then((res) => {
                console.log("App.vue /getUserMessage", res);
                if (res.status == 200) this.$store.commit("login", res.data);
                window.location.reload(); // im lazy
              })
              .catch((err) => {
                console.error("App.vue /getUserMessage", err);
                this.$store.commit("logout");
                this.$router.push({ name: "LoginView" });
              });
          }
        })
        .catch((err) => {
          console.error("HomeView.vue onAvatarUpload", err);
          this.$message.error({
            showClose: true,
            message: err.message,
          });
        });
    },

    /**
     * verify before upload avatar
     *
     * @param {File} file - file to be upload
     * @returns {boolean} wether to upload this file
     */
    beforeAvatarUpload(file) {
      const isImage = /^image/.test(file.type);
      const isLessThan2M = file.size / 1024 / 1024 < 2;
      if (!isImage) {
        this.$message.error({
          showClose: true,
          message: "请上传头像图片",
        });
      } else if (!isLessThan2M) {
        this.$message.error({
          showClose: true,
          message: "上传头像图片大小不能超过 2MB",
        });
      }
      return isImage && isLessThan2M;
    },

    clearUpdatePasswordForm() {
      this.updatePasswordForm.oldPassword = "";
      this.updatePasswordForm.newPassword = "";
      this.updatePasswordForm.confirmNewPassword = "";
    },
    updatePassword() {
      let passed;
      this.$refs.updatePasswordForm.validate((res) => (passed = res));
      console.log("HomeView.vue updatePassword() passed validation?", passed);
      if (!passed) return;

      this.updatePasswordForm.loading = true;

      const formData = new FormData();
      formData.append("oldPassword", this.updatePasswordForm.oldPassword);
      formData.append("newPassword", this.updatePasswordForm.newPassword);

      this.$http
        .post("/ModifyPassword", formData)
        .then((res) => {
          console.log("HomeView.vue updatePassword /ModifyPassword", res);
          this.$message({
            showClose: true,
            message: res.data,
            type: res.status == 200 ? "success" : "error",
          });
          if (res.status == 200) {
            this.updatePasswordForm.dialogVisible = false;
            this.clearUpdatePasswordForm();
          }
        })
        .catch((err) => {
          console.error("HomeView.vue updatePassword /ModifyPassword", err);
          this.$message({
            showClose: true,
            message: err.message,
            type: "error",
          });
        })
        .finally(() => (this.updatePasswordForm.loading = false));
    },
  },
};
</script>

<style lang="scss" scoped>
.home {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;
  .card {
    width: 50%;
    min-width: 700px;
    $avatar-width: 150px;
    $avatar-height: 150px;
    .avatar {
      width: $avatar-width;
      height: $avatar-height;
    }
    .avatar-uploader {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      overflow: hidden;
      width: $avatar-width;
      height: $avatar-height;
      &:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        // 这里的 width 和 height 是为了保证按钮的大小
        width: $avatar-width;
        height: $avatar-height;
        color: #8c939d;
        line-height: $avatar-width;
        text-align: center;
      }
    }
  }
}
</style>
